import {search} from '../../api/http.js'
export default {
	data() {
		return {
			searchItem:[]
		}
	},
	onLoad(){
		this.getSearchList()
	},
	onPullDownRefresh() {
		this.getSearchList()
	},
	methods: {
		random (lower, upper) {
			return Math.floor(Math.random() * (upper - lower)) + lower;
		},
		//随机获取列表1-6页内容
		getSearchList(){
			// console.log(this.random(1,6))
			let num = this.random(1,6)
			search(num).then(res=>{
				this.searchItem = res.data.rows
				console.log(res)
			})				
		}
	}
}
